@import "../../../styles/shared.scss";

.swiper {
  min-width: $mobile;
  height: 100vh;

  .swiper-button-prev {}

  .swiperSlide {
    display: flex;
    flex-direction: column;

    color: #fff;



    &:nth-child(2n) {
      flex-direction: column-reverse;


    }

    &__description {
      padding-top: 20px;
      text-align: center;
      height: 50%;
      background-color: #66c7cd;

    }



    &__title {
      margin-top: 10px;
      text-transform: uppercase;
    }

    &__list {
      margin-top: 20px;
    }

    &__item {
      padding: 5px 0;
      margin-left: 30px;
      margin-right: 30px;

      &:not(:first-child) {
        border-top: rgba(#fff, 0.1) 1px solid;

      }
    }

    &__img {
      height: 50%;
      background-color: black;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }

    &__img--manicure {
      background-image: url('../../../images/ServicesImg/manicure.png');
    }

    &__img--pedicure {
      background-image: url('../../../images/ServicesImg/pedicure.jpg');
    }

    &__img--depilacja {
      background-image: url('../../../images/ServicesImg/depilacja.jpg');
    }

    &__img--laser {
      background-image: url('../../../images/ServicesImg/laser.jpeg');
    }

    &__img--zabiegi_Na_Twarz {
      background-image: url('../../../images/ServicesImg/zabiegi-na-twarz.jpg');
    }

  }





  @media screen and (min-width: $tablet) {
    width: $tablet;
  }

  @media screen and (min-width: $desktop) {
    width: $desktop;
  }

  @media screen and (min-width: $desktopL) {
    width: $desktopL;
  }

  @media screen and (min-width: $desktop4k) {
    width: $desktop4k;
  }
}